*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

*:focus {
  outline: none;
}

* {
  padding: 0px;
  margin: 0px;
  border: none;
}

body {
  height: 100vh;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  font-size: inherit;
  font-weight: inherit;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

img {
  display: block;
}

img,
svg {
  max-width: 100%;
  height: auto;
}

ul,
ul li {
  list-style: none;
}

a,
a:link,
a:visited,
a:hover {
  color: inherit;
  text-decoration: none;
}

input,
textarea,
button,
select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
}

button,
input[type=submit] {
  display: inline-block;
  -webkit-box-shadow: none;
          box-shadow: none;
  background-color: transparent;
  background: none;
  cursor: pointer;
}

address {
  font-style: normal;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.work-item__btn-svg path,
.work-item__btn,
.header__lightmode-btn::after,
.footer__social-link,
.contacts__link {
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.work-item__img,
.portfolio__item {
  -webkit-box-shadow: 0 5px 35px var(--color-25p);
          box-shadow: 0 5px 35px var(--color-25p);
}

:root {
  --color-grey-0: hsl(0, 0%, 100%);
  --color-grey-1: hsl(214, 8%, 16%);
  --color-grey-2: hsl(0, 0%, 15%);
  --color-grey-3: hsl(240, 2%, 9%);
  --color-grey-4: hsl(0, 0%, 0%);
  --color-25p: rgba(0, 0, 0, 0.25);
  --color-brand: #5c62ec;
  --color-brand--dark: #4b50dd;
  --color-brand-5p: rgba(92, 98, 236, 0.05);
  --color-bg: var(--color-grey-0);
  --color-text: var(--color-grey-3);
  --color-btn: var(--color-grey-4);
  --margin-block: 70px;
}

.darkmode {
  --color-bg: hsl(0, 0%, 22%);
  --color-text: hsl(240, 2%, 90%);
  --color-btn: hsl(240, 2%, 90%);
  --color-25p: rgba(255, 255, 255, 0.25);
  --color-brand: #888cff;
  --color-brand--dark: #676cff;
}

body {
  background-color: var(--color-bg);
  font: 18px/1.5 "DM Sans", sans-serif;
  color: var(--color-text);
	overflow-y: scroll;
}

#root {
	min-height: 100vh;
}

.App {
	min-height: 100vh;
  min-width: 320px;
  overflow-x: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

.main {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
}

.container {
  max-width: 1200px;
  padding: 0 15px;
  margin: 0 auto;
}

.title {
  font-size: 60px;
  font-weight: 700;
  line-height: 1.3;
  letter-spacing: -0.5px;
  color: var(--color-brand);
  text-align: center;
}

.subtitle {
  font-size: 40px;
  font-weight: 700;
  line-height: 1.3;
}

.contacts__title {
  margin-top: var(--margin-block);
  margin-bottom: 40px;
}

.contacts__box {
  max-width: 570px;
  margin: 0 auto 40px;
  text-align: center;
}

.contacts__subtitle {
  margin-bottom: 20px;
}

.contacts__text + .contacts__text {
  margin-top: 0.8em;
}

.contacts__link:hover {
  color: var(--color-brand);
}

.footer {
  padding: 60px 0 50px;
  background-color: var(--color-grey-3);
}

.footer__social {
  margin-bottom: 28px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-column-gap: 30px;
     -moz-column-gap: 30px;
          column-gap: 30px;
}

.footer__social-link:hover {
  -webkit-transform: scale(1.15);
      -ms-transform: scale(1.15);
          transform: scale(1.15);
}

.footer__social-link:active {
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
}

.footer__social-icon path {
  fill: var(--color-grey-0);
}

.footer__copyright {
  font-size: 16px;
  text-align: center;
  color: var(--color-grey-0);
}

.header {
  color: var(--color-grey-0);
  background-color: var(--color-grey-3);
}

.header__inner {
  padding: 20px 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  gap: 20px;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  position: relative;
}

.header__logo {
  font: 24px/1 "Poppins", sans-serif;
}

.header__logo--bold {
  font-weight: 700;
}

.header__nav {
  width: 100%;
  max-width: 260px;
}

.header__nav-list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  gap: 15px;
}

.header__nav-link {
  font: 16px/1.5 "Poppins", sans-serif;
  font-weight: 500;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.header__nav-link--active {
  position: relative;
}

.header__nav-link--active::after {
  content: "";
  width: 100%;
  height: 3px;
  position: absolute;
  bottom: -3px;
  left: 0;
  background: var(--color-brand);
}

.header__nav-link:hover {
  text-shadow: 0 0 10px var(--color-grey-0);
}

.header__lightmode-btn {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0 5px;
  height: 26px;
  min-width: 52px;
  position: relative;
  background-color: var(--color-grey-2);
  border-radius: 13px;
}

.header__lightmode-btn::after {
  content: "";
  width: 24px;
  height: 24px;
  position: absolute;
  left: 1px;
  top: 1px;
  border-radius: 12px;
  background-color: var(--color-grey-0);
}

.header__lightmode-btn--darkmode::after {
  left: 27px;
}

.header__lightmode-svg {
  position: relative;
  z-index: 2;
}

.header__lightmode-svg path {
  fill: var(--color-grey-4);
}

.greetings {
  margin-bottom: var(--margin-block);
  background-color: var(--color-grey-3);
  border: 1px solid var(--color-grey-1);
}

.greetings__content {
  padding: 148px 0 40px;
  margin: 33px 0;
  min-height: 628px;
  background: center/contain no-repeat url("./../img/abstract-bg.svg");
}

.greetings__text {
  color: var(--color-grey-0);
  text-align: center;
}

.greetings__text--big {
  font-size: 60px;
  font-weight: 700;
  line-height: 1.4;
}

.greetings__text--medium {
  margin-bottom: 13px;
  font-size: 40px;
  font-weight: 700;
  line-height: 1.4;
}

.greetings__text--small {
  font-size: 18px;
  font-weight: 500;
  line-height: 24px;
}

.greetings__text--accent {
  color: var(--color-brand);
}

.greetings__btn {
  display: block;
  padding: 12px 29px;
  margin: 40px auto 0;
  font-size: 16px;
  font-weight: 500;
  color: var(--color-bg);
  background-color: var(--color-brand);
  border-radius: 5px;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.greetings__btn:hover,
.greetings__btn:focus {
  background-color: var(--color-brand--dark);
}

.greetings__btn:active {
  -webkit-transform: scale(0.95);
      -ms-transform: scale(0.95);
          transform: scale(0.95);
}

.portfolio {
  margin-bottom: var(--margin-block);
}

.portfolio__title {
  margin-bottom: 60px;
}

.portfolio__list {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: (1fr)[3];
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
}

.portfolio__item {
  max-width: 370px;
  border-radius: 10px;
}

.portfolio__item:hover {
  color: var(--color-brand);
  -webkit-box-shadow: 0 5px 35px var(--color-25p), inset 0 0 10px -5px var(--color-brand);
          box-shadow: 0 5px 35px var(--color-25p), inset 0 0 10px -5px var(--color-brand);
  background-color: var(--color-brand-5p);
}

.portfolio__item-img {
  width: 100%;
  border-radius: 10px;
  position: relative;
  z-index: -1;
}

.portfolio__item-title {
  padding: 15px 20px 25px;
  font-weight: 700;
  font-size: 24px;
  line-height: 1.3;
}

.skills__title {
  margin-top: var(--margin-block);
  margin-bottom: 40px;
}

.skills__box {
  max-width: 570px;
  margin: 0 auto 40px;
  text-align: center;
}

.skills__subtitle {
  margin-bottom: 20px;
}

.skills__text + .skills__text {
  margin-top: 0.8em;
}

.work-item__title {
  margin-top: var(--margin-block);
  margin-bottom: 40px;
}

.work-item__inner {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.work-item__img {
  margin-bottom: 40px;
  width: 100%;
  max-width: 870px;
  border-radius: 10px;
}

.work-item__text {
  margin-bottom: 30px;
  font-size: 24px;
  font-weight: 700;
  line-height: 1.3;
  text-align: center;
}

.work-item__btn {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  gap: 10px;
  margin-bottom: var(--margin-block);
  padding: 12px 20px;
  border-radius: 5px;
  border: 2px solid var(--color-btn);
  font-weight: 500;
}

.work-item__btn:hover {
  border: 2px solid var(--color-brand);
  color: var(--color-brand);
}

.work-item__btn:hover path {
  fill: var(--color-brand);
}

.work-item__btn:active {
  -webkit-transform: scale(0.9);
      -ms-transform: scale(0.9);
          transform: scale(0.9);
}

.work-item__btn-svg path {
  fill: var(--color-btn);
}

@media (max-width: 992px) {
  :root {
    --margin-block: 50px;
  }

  .title {
    font-size: 48px;
  }

  .subtitle {
    font-size: 32px;
  }

  .footer__social-link {
    padding: 10px;
  }

  .footer {
    padding: 30px 0;
  }

  .greetings__content {
    padding: 40px 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    min-height: auto;
  }

  .portfolio__item-title {
    text-align: center;
  }
}

@media (max-width: 900px) {
  .portfolio__list {
    -ms-grid-columns: (1fr)[2];
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
  }
}

@media (max-width: 768px) {
  .portfolio__item-title {
    font-size: 20px;
  }
}

@media (max-width: 600px) {
  .header__inner {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    gap: 20px;
  }

  .header__lightmode-btn {
    position: absolute;
    right: 0;
  }

  .greetings__text--big {
    font-size: 48px;
  }

  .greetings__text--medium {
    font-size: 34px;
  }
}

@media (max-width: 480px) {
  :root {
    --margin-block: 40px;
  }

  .greetings__text--big {
    font-size: 36px;
  }

  .greetings__text--medium {
    margin-bottom: 6px;
    font-size: 28px;
  }

  .portfolio__list {
    -ms-grid-columns: (1fr)[1];
    grid-template-columns: repeat(1, 1fr);
  }

  .work-item__text {
    font-size: 20px;
  }
}

@media (max-width: 420px) {
  .header__logo {
    max-width: 200px;
    text-align: center;
  }

  .header__lightmode-btn {
    top: 30px;
  }
}
